import {setToken,getToken} from "@/libs/util";
import {getLoginMessage,registGo,userLogout,loginByToken,userList,deleteUsers,emailCheck,loginByCo,updateOneUser} from "@/api/api";
import Cookies from 'js-cookie';
let user={
    state:{userEmail:'',userPwd:'',userName:'',token:'',userStatus:'',canteenId:'',userId:''},    
    mutations:{
        setuserEmail(state,userEmail){
            state.userEmail=userEmail;
        },
        setuserPwd(state,userPwd){
            state.userPwd=userPwd;
        },
        setuserName(state,userName){
            state.userName=userName;
        },
        setToken(state,token){
            state.token=token;
        },
        setuser(state,user){
            state.userEmail=user.userEmail;
            state.userPwd=user.userPwd;          
            state.userName=user.userName;
            state.token=user.token;
            state.userStatus=user.userStatus;
            state.canteenId=user.canteenId;
            state.userId=user.userId;           
        }
    },  
    actions:{
    login({commit},user){ 
        return new Promise((resolve,reject)=>{
            getLoginMessage(user).then(res=>{
                    let result= res.data.data
                    if(!result){
                        reject("登录失败")
                        return
                    }       
                    commit('setuser',result.user)
                    setToken(result.token);
                    localStorage.setItem('user',JSON.stringify(result.user));
                    resolve(result.user);
            },err=>{
                reject("登录失败");
            })
        });
},
    updateUser({commit},user){
        return new Promise((resolve,reject)=>{
            updateOneUser(user).then(res=>{
                let result= res.data.data
                    if(!result){
                        reject("更新失败")
                        return
                    }   
                    commit('setuser',result.user)
                    setToken(result.token);
                    localStorage.setItem('user',JSON.stringify(result.user));
                    resolve(result.user);
            },err=>{
                reject("更新失败");
            })
        })
    },
    //token登录获取登录信息
    tokenLogin({commit}){      
        return new Promise((resolve,reject)=>{
            const token=getToken();
            loginByToken(token).then(res=>{
                let result=res.data.data
                let userVuex=result.user
                commit('setuser',userVuex);
                setToken(result.token);
                localStorage.setItem('user',JSON.stringify(userVuex));            
                resolve(userVuex);
            },err=>{
                reject("信息错误");
            })
        })
    },
    //通过验证码登录
    loginByCode({commit},user){
        return new Promise((resolve,reject)=>{
            loginByCo(user).then(res=>{
                let result= res.data.data                
                    commit('setuser',result.user);
                    setToken(result.token);
                    localStorage.setItem('user',JSON.stringify(result.user));
                    resolve(result.user);
            },err=>{
                reject("登录失败");
            })
        })  
    },
    regist({commit},user){
        return new Promise((resolve,reject)=>{
            registGo(user).then(res=>{
                let userVuex=res.data.data;
                //commit('setuser',userVuex)
                //setToken(userVuex.token);
                resolve(userVuex);              
            },err=>{
                reject('注册失败');
            })
        });      
    },
    
    registSeller({commit},user){
        return new Promise((resolve,reject)=>{
            registGo(user).then(res=>{
                let userVuex=res.data.data;
                resolve(userVuex);
            },err=>{
                reject('注册失败');
            })
        });
    },

    logout({commit}){
        const token=getToken();       
        return new Promise((resolve,reject)=>{
            userLogout(token).then(res=>{
                if(res.data.status==201){
                    reject("退出失败");
                    return;
                }
                localStorage.clear();
                commit('setToken','');
                Cookies.remove('token');
                resolve("退出成功");
            },err=>{
                reject("退出失败");
            });
        })
    },

    userList({commit},user){
        return new Promise((resolve,reject)=>{
            userList(user).then(res=>{
                if(res.data.status==201){
                    reject("服务器内部出错");
                    return;
                }
                resolve(res.data.data);
            },err=>{
                reject("服务器内部出错");
            })
        }) 
    },

    usersDelete({commit},users){
        return new Promise((resolve,reject)=>{
            deleteUsers(users).then(res=>{
                if(res.data.status==201){
                    reject("删除失败");
                    return;
                }
                resolve("删除成功");
            },err=>{
                reject("服务器内部出错");
            })
        });
    },

    emailCheck({commit},user){
        return new Promise((resolve,reject)=>{
            emailCheck(user).then(res=>{
                if(res.data.status==201){
                    reject("发送验证码失败");
                    return;
                }
                resolve("发送验证码成功");
            }
        ,err=>{
            reject("服务器出错");
        })
    })
    },
}
}
export default user;
