<template>
 <div>
<template>
  <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
  <div style="margin: 15px 0;"></div>
  <el-checkbox-group v-model="manyCanteen" @change="handleCheckedCitiesChange">
    <el-checkbox v-for="city in canteens" :label="city" :key="city.canteenId">{{city.canteenName}}</el-checkbox>
  </el-checkbox-group>  
    <div style="margin: 15px 0;"></div>
    <el-button type="danger" @click="deleteList">删除</el-button>
</template>     
</div>   
</template>
<script>
export default {
     name:'DeleteCapo',
      data() {
      return {
        checkAll: false,
        canteens: [],
       manyCanteen:[],
        isIndeterminate: true
      };
    },
    methods: {
      handleCheckAllChange(val) {
        this.manyCanteen = val ? this.canteens : [];
        this.isIndeterminate = false;
       
      },
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.canteens.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.canteens.length;
      },
      deleteList(){//传递要删除的食堂id的集合
          let ca=this.manyCanteen;
          let arr=[];
          for(let i=0;i<ca.length;i++){
              arr.push(ca[i].canteenId);
          }
          if(arr.length==0){
            alert("请先选择数据");
            return;
          }
          const v=arr.toString();
          var params={"canteens":v};
          this.$emit("deleteCanteenList",params);
      }
    }
}
</script>

