    <template>
    <Form :model="canteen" label-position="left" :label-width="100">
            <FormItem label="食堂名称">
                <Input v-model="canteen.canteenName"></Input>
            </FormItem>
        <FormItem>
                <Button type="primary" @click="handleSubmit(canteen)">添加</Button>
            </FormItem>
    </Form>  
    </template>
    <script>
    import { mapActions } from 'vuex';
    export default {
    name: "AddCanteen",
    data() {
        return {
        canteen: {
            canteenName: "",
           
        }
        };
    },
    methods:{
        ...mapActions(["addCanteenObject"]),
        handleSubmit:function(canteen){
         // this.$emit('addCanteenObj',canteen);
           this.addCanteenObject(canteen)        
          .then(res=>{
              this.$Message.success(res);
                 this.$router.push("/main");
          },err=>{
              this.$Message.error(err);
          })
        }
    }
    };
    </script>
    <style lang="less">
    </style>

