                            Item 1
                        </MenuItem>
                        <MenuItem name="2">
                            <Icon type="ios-keypad"></Icon>
                            Item 2
                        </MenuItem>
                        <MenuItem name="3">
                            <Icon type="ios-analytics"></Icon>
                            <Dropdown style="margin-left: 20px">
        <Button type="primary">
            {{userName}}
            <Icon type="ios-arrow-down"></Icon>
        </Button>
        <DropdownMenu slot="list">
            <DropdownItem>个人中心</DropdownItem>
            <DropdownItem @click.native="logoutButton">退出登录</DropdownItem>
        </DropdownMenu>
    </Dropdown>
                        </MenuItem>
                        <MenuItem name="4">                           
                        </MenuItem>
                    </div>
                </Menu>
            </Header>
            <Layout :style="{padding: '0 50px'}">
