                </Breadcrumb>
                <Content :style="{padding: '24px 0', minHeight: '280px', background: '#fff'}">
                    <Layout>
                        <Sider hide-trigger :style="{background: '#fff'}">
                            <Menu active-name="1-2" theme="light" width="auto" :open-names="['1']">
                                <Submenu name="1">
                                    <template slot="title">
                                        <Icon type="ios-navigate"></Icon>
                                       食品管理
                                    </template>
                                    <MenuItem name="1-1" @click.native="addFood">添加食品</MenuItem>
                                    <MenuItem name="1-2" @click.native="updateFoodPage">修改食品信息</MenuItem>
                                    <MenuItem name="1-3" @click.native="deleteFoodPage">删除食品</MenuItem>
                                </Submenu>
                                <Submenu name="2">
                                    <template slot="title">
                                        <Icon type="ios-keypad"></Icon>
                                        店铺管理
                                    </template>
                                    <MenuItem name="2-1" @click.native="updateCanteen">修改店铺信息</MenuItem>
                                </Submenu>
                                <Submenu name="3">
                                    <template slot="title">
                                        <Icon type="ios-analytics"></Icon>
