                                    </template>
                                    <MenuItem name="3-1" @click.native="commentPage">所有评论</MenuItem>
                                </Submenu>
                                 <Submenu name="4">
                                    <template slot="title">
                                        <Icon type="ios-analytics"></Icon>
                                        订单管理
                                    </template>
                                    <MenuItem name="4-1" @click.native="sellerOrderPage(0)">未接单</MenuItem>
                                    <MenuItem name="4-2" @click.native="sellerOrderPage(1)">已接单</MenuItem>
                                    <MenuItem name="4-3"  @click.native="sellerOrderPage(2)">已完成</MenuItem>
                                </Submenu>
                            </Menu>
                        </Sider>
                        <Content :style="{padding: '24px', minHeight: '280px', background: '#fff'}">
                            <router-view>
                            </router-view>
                        </Content>
                    </Layout>
                </Content>
            </Layout>
            <Footer class="layout-footer-center">2011-2016 &copy; TalkingData</Footer>
        </Layout>
    </div>
</div>
