<script>
import { mapActions } from 'vuex';
export default {
    name:"SellerMain",
    data(){
        return{
            canteenId:this.$store.state.user.canteenId,
            userName:''
        }
    },
    mounted(){
         const user=this.$store.state.user;              
                  this.tokenLogin().then(res=>{ 
                      this.userName=res.userName                  
                    this.canteenId=res.canteenId
                    let data={canteenId:this.canteenId}
                    //作用是将食堂信息存到store里
                    this.selectOneCanteen(data).then(res=>{
                    },err=>{
                        this.$Message.error(err);
                    })
                  },err=>{
                      this.$Message.error(err);
                  })        
    },
    methods:{
         ...mapActions(['tokenLogin','selectOneCanteen','logout']),
        addFood(){
            this.$router.push({name:'addfood',query:{canteenId:this.canteenId}})
        },
        updateCanteen(){
                this.$router.push({name:'updateShop',query:{canteenId:this.canteenId}})
        },
        updateFoodPage(){
              this.$router.push({name:'foodList',query:{canteenId:this.canteenId}}) 
        },
        deleteFoodPage(){
             this.$router.push({name:'delfoodList',query:{canteenId:this.canteenId}}) 
        },
        logoutButton(){
            this.logout().then(res=>{
                this.$Message.success("退出成功")
                this.$router.push({name:'login'})   
            },err=>{
                this.$Message.error("退出失败")
            })
        },
        sellerOrderPage(num){
            this.$router.push({name:'sellerMain'}) 
                   setTimeout(() => {
                        this.$router.push({name:'sellerorder',query:{orderState:num}}) 
                   }, 3); 
        },
        commentPage(){
            this.$router.push({name:'sellercomment'})   
        }
    },
    created(){
       
    }
    
}
</script>
<style lang="less">
.ll{
    height:1200px;
}
.layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
}
.layout-logo{
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
}
.layout-nav{
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
}
