<template>
<div class="regist">
  <Card style="width:350px" class="card">
        <p slot="title">
            注册信息
        </p>
        <ul>
         <regist-form @regist="registGo" @judgeEmail="judgeEmail" :email="email" ref="fo"></regist-form>   
        </ul>
    </Card>
</div>
</template>
<script>
import RegistForm from "_c/regist-form";
import { mapActions } from 'vuex'
import {registEmailJudge} from "@/api/api.js"
export default {
    name:"regist",
    components:{RegistForm},
    data(){
      return{
       email:''
      }
    },
    methods:{
        ...mapActions(
      ['regist']
    ),      
        registGo(name){                     
        this.regist(name).then(res=>{
        //     let user=res;
        //     if(user.userStatus==1){
        //    this.$router.push("/managehome");
        // }
        // else if(user.userStatus==2){
        //   this.$router.push("/managehome");
        // }
        // else{
        //   this.$router.push("/home");
        // }   
        this.$Message.success("注册成功");
        this.$router.push({name:'login'});
        },err=>{
            this.$Message.error(err);
        })
        },
        judgeEmail(email){
          let data={email:email}
          registEmailJudge(data).then(res=>{
            if(res.data.status==201){
              this.$Message.error("该邮箱已被注册，请重新输入");
              this.$refs.fo.resetEmail();
            }
          },err=>{
            this.$Message.error("网络出错");
          })
        },
        
    },    
    mounted:function() {
         
        }, 
}
</script>
<style lang="less">
.regist{
    background-image: url('../../assets/6.jpg');
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100%;
    position: relative;
    .card{
     width:250px;
     display: inline-block;
     margin:200px 10px 0px 900px
   }
    
}
</style>

