<template >
<div class="right-buy"> 
<div class="order-div" >
<Icon type="ios-keypad-outline" size='30' class="order-icon" @mouseover.native='changeColor(1)'  :style="orderStyleObj" @mouseout.native="changeOutColor(1)"/>  
</div>
 <Divider />
 <div class="cart-div" :style="cartStyleObj"  @mouseover='changeColor(2)' @mouseout='changeOutColor(2)'>
  <Icon type="ios-cart-outline" size='30'/>  
  <b class="cart-b">购物车</b>
 </div>
  <Divider />
<div class="message-div">
<Icon type="ios-mail-outline" size='30' class="message-icon" @mouseover.native='changeColor(3)' :style="messageStyleObj"  @mouseout.native='changeOutColor(3)'/>
</div>
 <Drawer title="Basic Drawer" :closable="false" v-model="valueOrder">
        <p>Some contents...</p>
        <p>Some contents...</p>
        <p>Some contents...</p>
    </Drawer>
</div>    
</template>
<script>
export default {
    name:'RightBuy',
    data(){
        return{
            orderStyleObj:{ backgroundColor:"#FFFFFF"},
            messageStyleObj:{backgroundColor:"#FFFFFF"},
            cartStyleObj:{backgroundColor:"#FFFFFF"},
            valueOrder:false
        }
    },
    methods:{
        //鼠标移入icon元素时
        changeColor(state){
            
            if(state==1){
                this.orderStyleObj.backgroundColor="#33CCFF"
            }else if(state==2){
                 this.cartStyleObj.backgroundColor="#33CCFF"
                 
            }
            else if(state==3){
                this.messageStyleObj.backgroundColor="#33CCFF"
            }
        },
        //鼠标移除icon元素时
        changeOutColor(state){
            
             if(state==1){
                this.orderStyleObj.backgroundColor="#FFFFFF"
            }else if(state==2){
                 this.cartStyleObj.backgroundColor="#FFFFFF"
            }
            else if(state==3){
                this.messageStyleObj.backgroundColor="#FFFFFF"
            }
        }

    }
}
</script>
<style lang="less" scoped>
.right-buy{
    height: 100%;
    width:45px;
    background-color: #888888;
     position:absolute;
  right:0px;
  top:0px;
  z-index:1000;
    .order-div{
        height:30%;
        .order-icon{         
        margin:170px 0px 0px 7px;
        cursor: pointer;
        
    }
    }
    .message-div{
        height: 30%;
          .message-icon{
        background-color: white;
         margin:170px 0px 0px 7px;
         cursor: pointer;
    }
    }  
    .cart-div{
        cursor: pointer;
        height:20%;
        padding: 0px 0px 0px 12px;
        .cart-b{
            font-size:25px;

        }
    } 
}
</style>


