<template>
   <div class="layout">
        <Layout class='ll'>
            <Header>
                <Menu mode="horizontal" theme="dark" active-name="1">
                    <div class="layout-logo"></div>
                    <div class="layout-nav">
                        <MenuItem name="1">
                            <Icon type="ios-navigate"></Icon>
                            Item 1
                        </MenuItem>
                        <MenuItem name="2">
                            <Icon type="ios-keypad"></Icon>
                            Item 2
                        </MenuItem>
                        <MenuItem name="3">                         
                            首页
                        </MenuItem>
                           <Dropdown>
        <Button type="primary">
            {{userName}}
           <Icon type="ios-contacts" />
        </Button>
        <DropdownMenu slot="list">
            <DropdownItem>个人中心</DropdownItem>
            <DropdownItem @click.native="out">退出登录</DropdownItem>
        </DropdownMenu>
    </Dropdown>
                    </div>
                </Menu>
            </Header>
            <Layout :style="{padding: '0 50px'}">
                <Breadcrumb :style="{margin: '16px 0'}">
                    <BreadcrumbItem>Home</BreadcrumbItem>
                    <BreadcrumbItem>Components</BreadcrumbItem>
                    <BreadcrumbItem>Layout</BreadcrumbItem>
                </Breadcrumb>
                <Content :style="{padding: '24px 0', minHeight: '280px', background: '#fff'}">
                    <Layout>
                        <Sider hide-trigger :style="{background: '#fff'}">
                            <Menu active-name="1-2" theme="light" width="auto" :open-names="['1']">
                                <Submenu name="1">
                                    <template slot="title">
                                        <Icon type="ios-navigate"></Icon>
                                      食堂管理
                                    </template>                                   
                                    <MenuItem name="1-1"  @click.native="addCanteen">添加食堂信息</MenuItem>                         
                                    <MenuItem name="1-2" @click.native="editCanteenObj">修改食堂信息</MenuItem>
                                    <MenuItem name="1-3"  @click.native="deleteCanteenCom">删除食堂信息</MenuItem>                                    
                                </Submenu>
                               
                            </Menu>
                        </Sider>
                        <Content :style="{padding: '24px', minHeight: '280px', background: '#fff'}">
                          <!--<component ref="co" v-bind:is="currentTabComponent" @addCanteenObj='addCanteenObj' @sellerComponent='sellComponent' @regist='addSeller' @addRegistComponent='addRegistComponent' @sellListComponent='sellListComponent' @deleteCanteenList="deleteCanteenList" @deleteUsers="deleteUsers"></component>-->                          
                          <keep-alive :include="cache">
                           <router-view @addRegistComponent='addRegistComponent'>
                            </router-view>
                          </keep-alive>
                          <component  v-bind:is="currentTabComponent" @regist='addSeller'></component>
                        </Content>
                    </Layout>
                </Content>
            </Layout>
            <Footer class="layout-footer-center">2011-2016 &copy; TalkingData</Footer>
        </Layout>
    </div>
</div>
</template>
<script>
import { mapActions } from 'vuex';
import {AddCanteen,EditCanteen,SelectComponent,DeleteCapo} from "_c/canteen";
import RegistForm from "_c/regist-form";
import {SellerList} from "_c/seller";
export default {
    name:'Main',
    data(){
        return{
            currentTabComponent:'',
            userName:'',
            canteenId:0,//用来标记管理员正在编辑哪个食堂的信息
            cache:[]           
        }
    },
    components:{AddCanteen},
    methods:{
        ...mapActions(['logout','tokenLogin','addCanteenObject','regist',"registSeller","userList","selectCanteens","deleteCanteenObj","usersDelete"]),
       out(){
           this.logout().then(res=>{               
               this.$Message.success("退出成功");
             this.currentTabComponent='';
             this.$router.push({name:'login'})   
           },err=>{
               this.$Message.error(err);
           })
       },
       
       addCanteen(){  //加载添加食堂功能的页面      
           this.currentTabComponent='';
           this.$router.push({name:"addcanteen"});
           this.cache.push("addcanteen");
       },



       ////

       editCanteenObj(){//加载修改食堂的页面
       this.currentTabComponent='';
            this.$router.push({name:"editcanteen"});  
            this.cache.push("editcanteen");       
       },

               deleteCanteenCom(){//加载删除食堂的页面                  
           this.$router.push({name:'deletecapo'});          
       },
        ////

        addRegistComponent(canteenId){//添加卖家的页面，也就是表单      
            this.canteenId=canteenId;          
            this.currentTabComponent=RegistForm;
        },

          addSeller(user){//添加卖家的操作           
           user.canteenId=this.canteenId;
           user.userStatus=2;
           this.registSeller(user).then(res=>{
               this.$Message.success("添加成功");         
               this.currentTabComponent='';
           },err=>{
               this.$Message.success(res);
           })                    
       },
    },
    mounted(){
        const user=this.$store.state.user;       
        if(!user.userEmail){            
                  this.tokenLogin().then(res=>{                   
                      this.userName=res.userName;
                  },err=>{
                      this.$Message.error(err);
                  })  
        }
        else{
            this.userName=user.userName;//在主页面添加上用户名
        }
        
    }
   
}
</script>
<style lang="less">
@import url('./main.less');
</style>


