import  Main from "@/views/manage/main.vue";
import regist from "@/views/regist/regist.vue";
import SellerMain from "@/views/seller/seller-main.vue";
import ClientMain from "@/views/client/client-main.vue";
import login from "@/views/login/login.vue";
import {AddCanteen,EditCanteen,SelectComponent,DeleteCapo,SellerList} from "@/views/manage";
let routes=[
    {
    path:"/main",//管理员页面
    name:"main",
    component:Main,
    children:[
        {
            path:"addcanteen",
            name:"addcanteen",
            component:AddCanteen
        },
        {
            path:"editcanteen",
            name:"editcanteen",
            component:EditCanteen

        },
        {
            path:"selectcomponent/:id",
            name:"selectcomponent",
            component:SelectComponent,
            props:true
        },
        {
            path:"deletecapo",
            name:"deletecapo",
            component:DeleteCapo
        },
        {
            path:"sellerlist/:canteenid",
            name:"sellerlist",
            component:SellerList,
            prop:true
        },
        {
            path:'/updateShop',
            name:'adminUpdateShop',
            component:()=>import('@/views/seller/shop/updateShop.vue')
        },      
    ]
},

{ 
path:"/clientmain", //用户页面
name:'clientMain',
component:ClientMain,
children:[
    {
        path:'/canteenList',
        name:'canteenList',
        component:()=>import('@/views/client/canteen-list.vue')
    },
    {
        path:'/clientFoodList',
        name:'clientFoodList',
        component:()=>import('@/views/client/food-list.vue')
    },
    {
        path:'/clienthome',
        name:'clienthome',
        component:()=>import('@/views/client/client-home.vue'),
        children:[
            {
                path:'/addresspage',
                name:'addresspage',
                component:()=>import('@/views/client/address-page.vue')
            },
            {
                path:'/orderhome',
                name:'orderhome',
                component:()=>import('@/views/client/order-home.vue'),
            },
            {
                path:'/collectcanteen',
                name:'collectcanteen',
                component:()=>import('@/views/client/collect-canteen.vue')
            },
            {
                path:'/userupdate',
                name:'userupdate',
                component:()=>import('@/views/client/userupdate.vue')
            }
        ]
    },
    {
        path:'/orderpage',
        name:'orderpage',
        component:()=>import('@/views/client/order-page.vue'),
    },
    {
        path:'/commentpage',
        name:'commentpage',
        component:()=>import('@/views/client/comment-page.vue'),
    }

]
},     
        {
            path:"/login",
            component:login,
            name:"login"
        },
        {
            path:"/findPwd",
            name:"findpwd",
            component:()=>import('@/views/login/findpwd.vue'),           
        },
        {
        path:"/regist",
        component:regist,
        name:"regist"
        },
        
        //卖家页面
        {
            path:"/sellermain",//卖家页面
            component:SellerMain,
            name:'sellerMain',
            children:[
               {
                   path:'/addfood',
                   name:'addfood',
                   component:()=>import('@/views/seller/food/add-food.vue')
               },
               {
                   path:'/updateShop',
                   name:'updateShop',
                   component:()=>import('@/views/seller/shop/updateShop.vue')
               },
               {
                path:'/foodList',
                name:'foodList',
                component:()=>import('@/views/seller/food/food-list.vue')
               },
               {
                path:'updateFood',
                name:'updateFood',
                component:()=>import('@/views/seller/food/update-food.vue')
               },
               {
                path:'/delfoodList',
                 name:'delfoodList',
                 component:()=>import('@/views/seller/food/delfood-list.vue')
             },
             {
                path:'/sellerorder',
                name:'sellerorder',
                component:()=>import('@/views/seller/order/sellerOrder.vue')
             },
             {
                path:'/sellercomment',
                name:'sellercomment',
                component:()=>import('@/views/seller/comment/seller-comment.vue')
             },
               
            ]
         },
         {
            path:'/blank',
            name:'blank',
            component:()=>import('@/views/blank.vue')
         }   
];
export default routes;