import Vue from 'vue';
import VueI18n from 'vue-i18n';
import Cookies from 'js-cookie'
import Router from 'vue-router';
import zhCnLocale from 'iview/src/locale/lang/zh-CN';
import enUsLocale from 'iview/src/locale/lang/en-US';
import zhTwLocale from 'iview/src/locale/lang/zh-TW';
    /**
     * 返回的i18n在main.js文件中Vue.use({i18n: (key, value) => i18n.t(key, value)})则可运用
     * @param {*} zhCnLocale 中文的对象 像是{ home: '首页',  components: '组件'}这样
     * @param {*} zhTwLocale 繁体字的对象 
     * @param {*} enUsLocale 英文的对象
     */
    export const setVueI18n=(customZhCn,customZhTw,customEnUs)=>{
    Vue.use(VueI18n);
    // 自动根据浏览器系统语言设置语言
    const navLang = navigator.language
    const localLang = (navLang === 'zh-CN' || navLang === 'en-US') ? navLang : false
    let lang = localLang || localRead('local') || 'zh-CN'//如果localLang为空或者长度为零则选择localRead('local')，如果再这样则继续选择下一个
    Vue.config.lang = lang
    const messages = {
        'zh-CN': Object.assign(zhCnLocale, customZhCn),
        'zh-TW': Object.assign(zhTwLocale, customZhTw),
        'en-US': Object.assign(enUsLocale, customEnUs)
      }
      const i18n = new VueI18n({
        locale: lang,
        messages
      })
      return i18n;

};
    /**
     * 通过key获取localStorage中的的数据
     * @param {*} key 
     */
    export const localRead=(key)=>{
       return  localStorage.getItem(key) || '';//建议把local放在localStorage中
       
    }





 
    export const setToken = (token) => {
      Cookies.set(TOKEN_KEY, token, {expires:1})//活性为一天， 建议导入config,Cookies.set(TOKEN_KEY, token, {expires: config.cookieExpires || 1})
    }
    
    /**
     * 
     * @param {*} TOKEN_KEY key
     */
    export const getToken = (TOKEN_KEY) => {
      const token = Cookies.get(TOKEN_KEY)
      if (token) return token
      else return false
    }


    /**
     * 设置路由
     * @param {*} LOGIN_PAGE_NAME 登录页面的名字
     * @param {*} routes 对象，例如 {path: '/login',name: 'login',meta:{title: 'login',hideInMenu: true},component: ab}
     * @param {*} TOKEN_KEY 用户的key
     * @param {*} homeName 主页的名字
     */
  export const setRouter=(LOGIN_PAGE_NAME,routes,TOKEN_KEY,homeName) =>{
      Vue.use(Router);
  const router = new Router({
  routes,
  mode: 'history'
  });
  router.beforeEach((to, from, next) => {
    iView.LoadingBar.start()
    const token = getToken(TOKEN_KEY)
    if (!token && to.name !== LOGIN_PAGE_NAME) {   
      // 未登录且要跳转的页面不是登录页
      next({
        name: LOGIN_PAGE_NAME // 跳转到登录页
      })
    } else if (!token && to.name === LOGIN_PAGE_NAME) {
      // 未登陆且要跳转的页面是登录页
      next() // 跳转
    } else if (token && to.name === LOGIN_PAGE_NAME) {
      // 已登录且要跳转的页面是登录页
      next({
        name: homeName // 跳转到homeName页
      })
    } else {//已登录且要跳转的页面不是登录页,比如访问没有权限的页面时
     
    }
  });
  router.afterEach(to => {
    iView.LoadingBar.finish()
    window.scrollTo(0, 0)
  });
  return router;
    }



