<template>
  <Row :gutter="16">
        <Col span="6">
             <Button type="info" @click="addRegistComponent">添加食堂卖家</Button>
        </Col>
        <Col span="6">
             <Button type="info" @click="sellList">删除食堂卖家</Button>
        </Col>
        <Col span="6">
             <Button type="info" @click="updatePage">修改食堂信息</Button>
        </Col>
    </Row> 
</template>
<script>
export default {
    name:'SelectComponent',
    data(){
        return{
            canteenid:''
        }
        
    },
    methods:{
        addRegistComponent:function(){                   
           this.$router.push("/main");
           this.$emit("addRegistComponent",this.canteenid);
        },
        sellList(){
            this.$router.push({name:"sellerlist",params:{canteenid:this.canteenid}});
        },
        updatePage(){
         this.$router.push({name:"adminUpdateShop",query:{canteenId:this.canteenid}});
        }
    },
    mounted(){
        this.canteenid=this.$route.params.id;  
    }
}
</script>
