<template>
<div class="client-main"> 
      <Menu mode="horizontal" :theme="theme1" active-name="1" >
          <Row class='menu-row'>
              <Col span="5" >
        <MenuItem name="1">
            西科食堂
        </MenuItem>
        </Col>
        <Col span="5">
        <MenuItem name="2" @click.native="homePageButton">          
            首页
        </MenuItem>
        </Col>
        <Col span="5">
        <MenuItem name="4" @click.native='orderHomeButton'>          
            我的订单
        </MenuItem>
        </Col>
                <Col span="8">
        <Submenu name="3">
            <template slot="title">
               {{userName}}
            </template>
                <MenuItem name="3-4" @click.native="clientHomeButton">个人中心</MenuItem>
                <MenuItem name="3-5" @click.native="logoutButton">退出登录</MenuItem>
        </Submenu>
        </Col>
        </Row>
    </Menu>   
     <Content>                                  
                        <router-view >
                       </router-view>
     </Content> 
     <Footer class="layout-footer-center">2011-2016 &copy; TalkingData</Footer> 
</div>
</template>
<script>
import { mapActions } from 'vuex';
import { setTimeout } from 'timers';
import RightBuy from "_c/right-buy/index.js"
export default {
name:"ClientMain",
 data () {
            return {
                theme1: 'primary',
                userName:'',
            }
        },
          components:{RightBuy},
        methods:{
            ...mapActions(['tokenLogin','logout']),
            logoutButton(){
                this.logout().then(res=>{
                     this.$Message.success("退出成功")
                    this.$router.push({name:'login'})
                },err=>{
                    this.$Message.error("退出失败")
                })
            },
            homePageButton(){
                   this.$router.push({name:'blank'}) 
                   setTimeout(() => {
                        this.$router.push({name:'canteenList'})
                   }, 1);                              
            },
            clientHomeButton(){
                    this.$router.push({name:'clienthome'})
            },
            orderHomeButton(){
                this.$router.push({name:'orderhome'})
            }
        },
        mounted(){ 
            //如果页面刷新，则需要重新登录一次，因为store里的数据刷新了就会不见        
            if(!this.$store.state.user.userEmail){
                    this.tokenLogin().then(res=>{
                        this.userName=res.userName
                    })
            }else{
                this.userName=this.$store.state.user.userName
            }
        //this.$router.push({name:'canteenList'})
        }
}
</script>
<style lang="less" >
@import url('./client-main.less');
</style>

