<template>

<div>
<template>
  <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
  <div style="margin: 15px 0;"></div>
  <el-checkbox-group v-model="users" @change="handleCheckedCitiesChange">
    <el-checkbox v-for="cc in allUser" :label="cc" :key="cc.userId">{{cc.userName}}</el-checkbox>
  </el-checkbox-group>
  <div style="margin: 15px 0;"></div>
  <el-button type="danger" @click="deleteUsers">删除按钮</el-button>
</template>
</div>
</template>
<script>
import { mapActions } from 'vuex';
export default {
  
    name:"SellerList",
    data(){
        return{
                users: [],
                allUser:[],//所有用户对象的集合
                checkAll: false,
                isIndeterminate: true
        }
    },
    methods: {

      ...mapActions(["usersDelete","userList"]),
          handleCheckAllChange(val) {
        this.users = val ? this.allUser : [];
       
        this.isIndeterminate = false;
      },
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.allUser.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.allUser.length;
      },
      deleteUsers(){//删除用户的集合
        let arr=[];
        let uu=this.users;
        for(let i=0;i<uu.length;i++){
                arr.push(uu[i].userId);
        }
        if(arr.length==0){
            alert("请先选择数据");
            return;
        }
        let u1=arr.toString();
        let params={users:u1};
         this.usersDelete(params).then(res=>{
              this.$Message.success(res);
             this.$router.push({name:"main"});
          },err=>{
              this.$Message.error(err);
          })
      }
        },
        mounted(){
           let user={canteenId:this.$route.params.canteenid};
            this.userList(user).then(res=>{
                this.allUser=res;                
            },err=>{
                this.$Message.error("亲，有小bug哦");
            })
        }

}
</script>
