<template>
<div>
  <Row :gutter="16">
        <Col span="6" v-for="v1 in canteenList" :key="v1.canteenId" class="canteenButton">
             <Button type="success" @click="addSeller(v1.canteenId)">{{v1.canteenName}}</Button>
        </Col>
    </Row>
    
    </div>
</template>
<script>
import { mapActions } from 'vuex'
export default {
    name:'EditCanteen',
    data(){
      return {
        canteenList:'',
         
      }
    },
    methods:{
      ...mapActions(['selectCanteens']),
      addSeller:function(canteenId){
        
        this.$emit('sellerComponent',canteenId);
      },
      
      
    },
    mounted(){
      this.selectCanteens(null).then(res=>{
        this.canteenList=res;
      },err=>{
        this.Message.error(err);
      });
    },
    
  }
 </script>

<style lang="less">
.canteenButton{margin-bottom:20px}
</style>
