    <template>
    <Form :model="canteen" label-position="left" :label-width="100">
            <FormItem label="食堂名称">
                <Input v-model="canteen.canteenName"></Input>
            </FormItem>
        <FormItem>
                <Button type="primary" @click="handleSubmit(canteen)" >Signin</Button>
            </FormItem>
    </Form>  
    </template>
    <script>
    export default {
    name: "AddCanteen",
    data() {
        return {
        canteen: {
            canteenName: "",
           
        }
        };
    },
    methods:{
        handleSubmit:function(canteen){
          this.$emit('addCanteenObj',canteen);
        }
    }
    };
    </script>
